<template>
  <div class="hs-container" style="background-image: url('static/image/login/bg.png')">
    <hs-scroll class="table-view">
      <ul>
        <div class="body-view" :style="{height: `${bodyHeight}px`}">
          <img class="homsom" src="static/image/login/logo.png" :style="{'margin': `${bodyHeight / 11}px 0`}"/>
          <div class="section">
            <div class="details">
              <div class="item">
                <div class="logo" style="background-image: url('static/image/login/company-.png')"></div>
                <input maxlength='50' v-model="cmpIdOrShortName" :placeholder="$t('base.PleaseEnterTheUnitCodeOrUnitShortName')"/>
              </div>
              <div class="item">
                <div class="logo" style="background-image: url('static/image/login/user-.png')"></div>
                <input maxlength='50' v-model="userName" :placeholder="$t('base.PleaseEnterAUsername')"/>
              </div>
              <div class="item">
                <div class="logo" style="background-image: url('static/image/login/password-.png')"></div>
                <input maxlength='50' v-model="password" type="password" :placeholder="$t('base.PleaseEnterThePassword')"/>
              </div>
              <div class="item">
                <div class="auto-login" @click="isAuto = !isAuto">
                  <!-- <div class="check" :style="{'background-image': isAuto ? `url('static/image/login/tick.png')` : ''}"></div>
                  <p>自动登录</p> -->
                </div>
                <p @click="$router.push('/loginWayOfRecovery')">{{$t('base.ForgetPassword')}}?</p>
              </div>
            </div>
            <hs-gradient-button type="full" :title="$t('base.Login')" @click="_gotoHome"></hs-gradient-button>
            <p class="tips">{{$t('base.CurrentVersion')}}: v{{version}}</p>
          </div>
        </div>
      </ul>
    </hs-scroll>
    <!-- 控制版本 需要和后端版本信息保持一致 !!!!-->
    <div ref="GetVersion" class="getVersion" title="1.3.0.4"></div>
  </div>
</template>
<script type="text/ecmascript-6">
// 登录页面
import {kpiLoginUserLogin, kpiHomeGetversionnumber} from 'api'
import {hex_sha1} from 'common/js/sha1' // eslint-disable-line
import {mapMutations} from 'vuex'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
export default {
  beforeRouteEnter(to, from, next) {
    if (
      to.path === '/forgetPasswordStep3'
    ) {
      next(false)
    } else {
      next()
    }
  },
  mounted() {
    this.$nextTick(() => {
      let height = document.body.getBoundingClientRect().height
      this.bodyHeight = height
    })
    this._checkVersion()
  },
  computed: {
    version() {
      return window.localStorage.version
    }
  },
  data() {
    return {
      cmpIdOrShortName: '', // 000003
      userName: '', // 0456
      password: '', // homsom
      isAuto: true,
      bodyHeight: 0
    }
  },
  methods: {
    ...mapMutations([
      'SET_UN_HANDLE_VETTING_COUNT',
      'SET_PERSONAL_CENTER_DATA'
    ]),
    /** 检查校对版本号 */
    _checkVersion() {
      kpiHomeGetversionnumber().then((res) => {
        console.log('登录页检查校对版本号: ', res)
        let version = this.$refs.GetVersion.title
        if (res.ResultData !== version) {
          window.location.reload()
        } else {
          window.localStorage.version = version
        }
      })
    },
    _gotoHome() {
      this.SET_UN_HANDLE_VETTING_COUNT(0)
      this.SET_PERSONAL_CENTER_DATA({})
      let params = {
        CmpIdOrShortName: this.cmpIdOrShortName,
        UserName: this.userName,
        Password: hex_sha1(this.password)
      }
      if (this.noValue(this.cmpIdOrShortName)) {
        this.$toast(this.$t('TM.PleaseEnter', 'function', this.$t('base.UnitNumberOrUnitName')))
        return
      } else if (this.noValue(this.userName)) {
        this.$toast(this.$t('base.PleaseEnterAUsername'))
        return
      } else if (this.noValue(this.password)) {
        this.$toast(this.$t('TM.PleaseEnter', 'function', this.$t('base.Password')))
        return
      }
      kpiLoginUserLogin(params).then((res) => {
        console.log('登录: ', res)
        this.$router.replace('/home')
      })
    }
  },
  components: {
    HsScroll,
    HsGradientButton
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  @extend .flex-column
  @extend .flex-start
  width 100%
  height 100%
  background-size 100% 100%
  >.table-view
    width 100%
    height 100%
    overflow hidden
    .body-view
      @extend .flex-column
      @extend .flex-start
      width 100%
      .homsom
        width 50%
      ul
        @extend .flex-column
        @extend .flex-start
      .section
        @extend .flex-column
        @extend .flex-start
        width calc(100% - 40px)
        background-color $color_white
        border-radius 10px
        padding 30px 20px
        .details
          @extend .flex-column
          @extend .flex-start
          width 100%
          >.item
            @extend .flex-row
            @extend .flex-between
            width 100%
            height 50px
            border-bottom 1px solid $color_theme
            &:last-child
              border-bottom 0px
              font-size 1rem
              color $color_gray_text
            >.logo
              width 22px
              height 22px
              background-size 100% 100%
            >input
              width calc(100% - 27px)
              height 100%
            >.auto-login
              @extend .flex-row
              @extend .flex-start
              >.check
                width 13px
                height 13px
                background-size 100% 100%
                border 1px solid $color_gray_layer
                border-radius 2px
                overflow hidden
                margin-right 8px
        .tips
          font-size 0.8rem
          color $color_gray_text
          line-height 1rem
          margin-top 10px
  >.getVersion
    @extend .disabled
    width 0px
    height 0px
    opacity 0
</style>



